.container{
    margin-top: 37px;
    margin-bottom: 30px;
    display:flex;
    flex-direction:column;
    width:100%;
    align-items: center;
}

.subContainer{
    display:flex;
    flex-direction:row;
    background-color: #fffeff;
    width: 70%;
    min-width: 1000px;
    padding: 20px;
    margin: 10px;
    border-radius: 30px;
    transition: transform 0.35s ease, box-shadow 0.35s ease;
}

@media (prefers-color-scheme: dark) {
    .subContainer {
      background-color: #1a1a1a;
    }
  }

.subContainer:hover{
    transform: scale(1.1); 
    box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.3);
    z-index: 3;
}

@media (prefers-color-scheme: dark) {
    .subContainer:hover {
        box-shadow: 0 0 20px 0px rgba(239, 238, 238, 0.566);
    }
  }

.scHalf{
    display: flex;
    flex-direction: column;
    //align-items: center;
    justify-content: center;
    //background-color: yellow;
    width: 50%;
    padding: 10px;
    > h1{
        display:flex;
        align-items: center;
    }
}




.scTercio{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    //background-color: yellow;
    width: 33%;
    padding: 10px;
}

.img{
    border-radius: 30px;
    width: 100%;
}

.img2{
    border-radius: 30px;
    max-width: 150px;
    transition: transform 0.35s ease, box-shadow 0.35s ease;
}

.img2:hover{
    transform: scale(1.1); 
    box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.3);
    z-index: 3;
}

@media (prefers-color-scheme: dark) {
    .img2:hover {
        box-shadow: 0 0 20px 0px rgba(239, 238, 238, 0.566);
    }
  }